
import { defineComponent, reactive, toRaw, toRefs, onMounted } from "vue";
import { useRouter } from "vue-router";
import { ImagePreview } from "vant";
import Epub from "epubjs";
import catalogue from "../components/catalogue.vue";
import settingUp from "../components/settingUp.vue";
import loading from "../components/loading.vue";
export default defineComponent({
  name: "xmlEpubReadPc",
  components: {
    catalogue,
    settingUp,
    loading,
  },
  props: {
    bookPath: {
      //电子书路径
      type: String,
      default: "",
    },
  },
  setup(props, context) {
    let data = reactive({
      book: Object,
      rendition: Object,
      flow: "auto",
      navigation: Array,
      bookAvailable: false,
      navShow: false,
      locations: Object,
      themeShow: false,
      defaultFontSize: 16,
      defaultFontFamily: 0,
      themes: Object,
      defaultTheme: 0,
      loadingShow: true,
      progress: 0,
      visibility: "",
      nowtitle: "",
      // filePath: 'https://ouchn-prod-public.oss-cn-beijing.aliyuncs.com/goods_file/fd9dc000-82d9-4f98-88ec-3c2f2459eeff.epub'
      filePath:
        "https://ouchn-prod-public.oss-cn-beijing.aliyuncs.com/goods_file/beea6394-0553-4b53-8738-6ad2847f187f.epub",
    });

    const themeList = [
      {
        name: "default",
        style: {
          body: {
            color: "#000",
            background: "#f8f4e9",
            backdrop: "#e5e1d6",
          },
        },
      },
      {
        name: "eye",
        style: {
          body: {
            color: "#000",
            background: "#dbcfa5",
            backdrop: "#cec29b",
          },
        },
      },
      {
        name: "gold",
        style: {
          body: {
            color: "#000",
            background: "#ebf1e5",
            backdrop: "#d5dad0",
          },
        },
      },
      {
        name: "cyan",
        style: {
          body: {
            color: "#000",
            background: "#e9f5f5",
            backdrop: "#d5dfdf",
          },
        },
      },
      {
        name: "pink",
        style: {
          body: {
            color: "#000",
            background: "#f4e9e9",
            backdrop: "#e2d9d9",
          },
        },
      },
      {
        name: "gray",
        style: {
          body: {
            color: "#000",
            background: "#e6e2e2",
            backdrop: "#d6d3d3",
          },
        },
      },
      {
        name: "night",
        style: {
          body: {
            color: "#fff",
            background: "#424242",
            backdrop: "#333333",
          },
        },
      },
    ];

    // 获取路由参数
    const router = useRouter().currentRoute.value.query;

    // 重缓存中读取主题
    if (localStorage.getItem("epub-theme-pc")) {
      let them = localStorage.getItem("epub-theme-pc");
      data.defaultTheme = JSON.parse(them || "").defaultTheme;
      data.defaultFontFamily = JSON.parse(them || "").defaultFontFamily;
      data.defaultFontSize = JSON.parse(them || "").defaultFontSize;
      data.flow = JSON.parse(them || "").flow;
    }

    // 电子书的解析和渲染
    function showEpub() {
      data.book = new (Epub as any)(props.bookPath);
      const Book = toRaw(data.book as any);
      data.rendition = Book.renderTo("epubReadBox", {
        width: window.innerWidth - 120,
        height: window.innerHeight - 60,
        // manager: "continuous",
        // allowScriptedContent: true, //是否允许插入外部文件
        // spread: "none",
        // flow: "scrolled-doc",
        flow: "scrolled-doc",
        manager: "continuous",
      });
      // 通过Rendtion.display渲染电子书
      let readingPosition = router.location
        ? router.location
        : localStorage.getItem("location_" + router.fileId) || "";

      if (readingPosition) {
        toRaw(data.rendition as any).display(readingPosition);
      } else {
        toRaw(data.rendition as any).display();
      }

      // 获取Theme对象
      data.themes = toRaw(data.rendition as any).themes;
      // 设置默认字体
      setFontSize(data.defaultFontSize);
      // 注册主题
      // registerTheme();
      // 设置默认主题
      setTheme(data.defaultTheme);
      toRaw(data.rendition as any).on("relocated", (location: any) => {
        getReadingPosition(location.start);
        if (location.atEnd) {
          data.visibility = "next";
        } else if (location.atStart) {
          data.visibility = "prev";
        } else {
          data.visibility = "";
        }
      });
      // 绑定点击事件
      toRaw(data.rendition as any).on("click", (e: any) => {
        data.navShow = false;
        data.themeShow = false;
        if (e.srcElement.tagName === "IMG") {
          //如果点击的是图片，查看大图
          ImagePreview({
            images: [e.srcElement.src],
            showIndex: false,
            closeable: true,
          });
        }
      });
      // Book对象的钩子函数ready
      Book.ready
        .then(() => {
          data.navigation = toRaw(Book.navigation.toc);
          // 生成Locations对象
          return Book.locations.generate();
        })
        .then(() => {
          // 保存locations对象
          data.locations = Book.locations;
          // 标记电子书为解析完毕状态
          setTimeout(() => {
            const currentLocation = toRaw(
              data.rendition as any
            ).currentLocation();
            getReadingPosition(currentLocation.start);
            data.bookAvailable = true;
            data.loadingShow = false;
          }, 2000);
        });
    }

    // 获取当前阅读位置
    function getReadingPosition(start: any) {
      data.nowtitle = start.href;
      if (toRaw(data.locations as any)._locations) {
        const progress =
          Math.floor(
            toRaw(data.locations as any)
              .percentageFromCfi(start.cfi)
              .toFixed(5) * 10000
          ) / 100;
        data.progress = progress;
      }

      if (router.fileId) {
        localStorage.setItem("location_" + router.fileId, start.cfi);
      }
    }

    // 上一页
    function prevPage() {
      if (data.rendition) {
        toRaw(data.rendition as any).prev();
      }
    }

    // 下一页
    function nextPage() {
      if (data.rendition) {
        toRaw(data.rendition as any).next();
      }
    }

    // 注册主题
    // function registerTheme() {
    //   themeList.forEach((theme: { name: any; style: any; }) => {
    //     toRaw(data.themes as any).register(theme.name, theme.style);
    //   });
    // }

    // 设置主题
    function setTheme(index: number) {
      // toRaw(data.themes as any).select(themeList[index].name);
      data.defaultTheme = index;
      result();
      saveTheme();
    }

    // 设置字号大小
    function setFontSize(fontSize: number) {
      data.defaultFontSize = fontSize;
      if (data.themes) {
        toRaw(data.themes as any).fontSize(fontSize + "px");
        saveTheme();
      }
    }

    // 设置字体
    function fontFamily(e: any) {
      toRaw(data.themes as any).font(e.value);
      data.defaultFontFamily = e.index;
      saveTheme();
    }

    // 切换阅读模式
    function readPattern() {
      data.loadingShow = true;
      if (data.flow === "auto") {
        const currentLocation = toRaw(data.rendition as any).currentLocation();
        setTimeout(() => {
          data.flow = "scrolled";
          toRaw(data.book as any).rendition.flow("scrolled");
        }, 800);
        setTimeout(() => {
          saveTheme();
          toRaw(data.rendition as any)
            .display(currentLocation.start.cfi)
            .then(() => {
              data.loadingShow = false;
            });
        }, 2000);
      } else {
        data.flow = "auto";
        toRaw(data.book as any).rendition.flow(data.flow);
        setTimeout(() => {
          saveTheme();
          data.loadingShow = false;
        }, 2000);
      }
    }

    // 关闭目录
    function closeNav() {
      data.navShow = false;
    }

    // 跳转到当前目录位置
    function jumpDirectory(item: any) {
      data.navShow = false;
      data.loadingShow = true;
      if (data.flow === "scrolled") {
        data.flow = "auto";
        toRaw(data.book as any).rendition.flow("auto");
        toRaw(data.rendition as any).display(item.href);
        let currentLocation = Object;
        setTimeout(() => {
          currentLocation = toRaw(data.rendition as any).currentLocation();
          toRaw(data.book as any).rendition.flow("scrolled");
        }, 800);
        setTimeout(() => {
          toRaw(data.rendition as any).display(
            toRaw(currentLocation as any).start.cfi
          );
          data.loadingShow = false;
        }, 2000);
      } else {
        toRaw(data.rendition as any).display(item.href);
        setTimeout(() => {
          data.loadingShow = false;
        }, 2000);
      }
    }

    // 关闭设置
    function closeTheme() {
      data.themeShow = false;
    }

    const onChange = (progress: number) => {
      const percentage = progress / 100;
      const location =
        percentage > 0
          ? toRaw(data.locations as any).cfiFromPercentage(percentage)
          : 0;
      toRaw(data.rendition as any).display(location);
    };

    function result() {
      toRaw(data.rendition as any).themes.default({
        "::selection": {
          background: "rgba(255,255,0, 0.3)",
        },
        ".epubjs-hl": {
          fill: "yellow",
          "fill-opacity": "0.3",
          "mix-blend-mode": "multiply",
        },
        body: {
          background: "rgba(255,255,255, 0)",
          "max-width": "100%vw important",
          "background-image": "none",
          color: themeList[data.defaultTheme].style.body.color,
        },
        ".tp-c": {
          "text-indent": "0",
        },
        "h1,h2,h3,h4,h5,h6": {
          "font-size": "inherit!important",
        },
        p: {
          "text-indent": "0",
          "line-height": "2em",
          "word-break": "break-word",
          "font-size": "inherit!important",
        },
        img: {
          "max-width": "100%!important",
        },
        video: {
          "max-width": "100%!important",
        },
        pre: {
          "white-space": "pre-wrap",
          "word-wrap": "break-word",
        },
      });
    }

    // 保存主题到缓存中
    function saveTheme() {
      let theme = {
        defaultTheme: data.defaultTheme,
        defaultFontFamily: data.defaultFontFamily,
        defaultFontSize: data.defaultFontSize,
        flow: data.flow,
      };
      localStorage.setItem("epub-theme-pc", JSON.stringify(theme));
    }

    onMounted(() => {
      showEpub();
    });

    return {
      data,
      ...toRefs(data),
      prevPage,
      nextPage,
      closeNav,
      jumpDirectory,
      themeList,
      setTheme,
      closeTheme,
      setFontSize,
      fontFamily,
      readPattern,
      onChange,
      // showEpub
    };
  },
});
