
import { defineComponent, getCurrentInstance } from "vue";
import { Toast } from "vant";
import { themeList } from "../state/ts/config";
export default defineComponent({
  name: "switchover",
  props: {
    defaultTheme: {
      type: Number,
      default: 0,
    },
    realPrice: {
      type: Number,
      default: 0,
    },
    haveBuy: {
      type: Boolean,
      default: false,
    },
    sourceList: {
      //商品资源列表
      type: Array,
      default: [],
    },
  },
  emits: ["close"],
  setup(props, context) {
    const myUni =
      getCurrentInstance()?.appContext.config.globalProperties.$myUni;
    function close() {
      context.emit("close", false);
    }

    function tabbook(item: any, index: any) {
      myUni.postMessage({
        data: {
          name: "changewenjian",
          sourceInfo: item,
          index: index,
        },
      });
    }

    return {
      themeList,
      close,
      tabbook,
    };
  },
});
