
import {
  defineComponent,
  reactive,
  toRaw,
  toRefs,
  watch,
  ref,
  onMounted,
  getCurrentInstance,
} from "vue";
import { ImagePreview, Dialog } from "vant";
import Epub from "epubjs";
import titleBar from "../components/titleBar.vue";
import menuBar from "../components/menuBar.vue";
import loading from "../components/loading.vue";
import inputField from "../components/inputField.vue";
import switchover from "../components/switchover.vue";
import { themeList } from "../state/ts/config";
import { Toast } from "vant";
export default defineComponent({
  name: "XmlEpubRead",
  components: { titleBar, menuBar, loading, inputField, switchover },
  props: {
    fileId: {
      //文件ID
      type: String,
      default: "",
    },
    bookPath: {
      //电子书路径
      type: String,
      default: "",
    },
    flow: {
      type: String,
      default: "paginated", //scrolled：流式阅读，paginated：翻页阅读
    },
    location: {
      //开始阅读的位置
      type: String,
      default: "",
    },
    notesList: {
      //笔记列表
      type: Array,
      default: [],
    },
    bookmarkList: {
      //书签列表
      type: Array,
      default: [],
    },
    lineationList: {
      //划线列表
      type: Array,
      default: [],
    },
    sourceList: {
      //商品资源列表
      type: Array,
      default: [],
    },
    luminance: {
      //菜单栏是否显示亮度
      type: String,
      default: "luminance",
    },
    limitSize: {
      //限制可阅读的百分比
      type: Number,
      default: 100,
    },
    isNote: {
      // 是否可以添加修改笔记
      type: String,
      default: "yes",
    },
    innerWidth: {
      type: Number,
      default: 375,
    },
    innerHeight: {
      type: Number,
      default: 500,
    },
    integralBox: {
      type: Boolean,
      default: false,
    },
    integral: {
      type: Number,
      default: 15,
    },
    width: {
      type: Number,
      default: 22, //max:80
    },
    systemType: {
      type: String,
      default: "",
    },
  },
  emits: [
    "confirmAddNotes",
    "confirmAmendNotes",
    "confirmDeleteNotes",
    "addBookmark",
    "deleteBookmark",
    "addUnderline",
  ],
  setup(props, context) {
    let data = reactive({
      book: Object,
      rendition: Object,
      navigation: [], //book目录
      nowtitle: "", //当前阅读的章节title
      locations: Object, //
      flow: props.flow,
      loadingShow: true,
      isPageTop: false, //页面是否在顶部
      isPageBottom: false, //页面是否在底部
      touchStartX: 0, //开始触摸X轴位置
      touchStartY: 0, //开始触摸Y轴位置
      touchStartTime: 0, //开始触摸时间
      dragLocation: "", //拖拽时的方向 prevChapter：上一章，nextChapter：下一张
      dragDistance: 0, //拖拽的距离
      titleBarShow: false, //titleBar显隐
      menuBarShow: false, //菜单显隐
      defaultFontSize: 12, //默认字体大小
      defaultFontFamily: 0, //默认字体索引
      defaultTheme: 0, //默认主题
      readProgress: 0,
      setUpShow: false,
      showProgress: false,
      showBrightness: false,
      toolbarStyle: { top: "50%", left: "50%" },
      showToolbar: false, //工具栏显隐
      cfiRange: "",
      showAddNotes: false,
      modifyNotes: false, //是否显示修改笔记
      modifyNotesType: "",
      notelsid: "", //笔记Id
      notesContent: "", //笔记内容
      noteState: "add", // 添加笔记修改笔记 add:添加，amend：修改
      bookNote: { noteContent: String }, // 要修改的笔记对象
      showSwitchover: false,
      searchModule: false,
      searchValue: "",
      searchList: [] as any[],
      menuBarBox: getCurrentInstance(),
      currentCfi: { cfi: "", href: "" },
      textRange: "",
    });

    const myUni =
      getCurrentInstance()?.appContext.config.globalProperties.$myUni;
    // const menuBar=getCurrentInstance()
    // const menuBarBox = getCurrentInstance();

    if (localStorage.getItem("xml-mobile-epub-theme")) {
      let them = localStorage.getItem("xml-mobile-epub-theme");
      data.defaultTheme = JSON.parse(them || "").defaultTheme;
      data.defaultFontFamily = JSON.parse(them || "").defaultFontFamily;
      data.defaultFontSize = JSON.parse(them || "").defaultFontSize;
      if (props.systemType != "ios") {
        data.flow = JSON.parse(them || "").flow;
      }
    }
    watch(
      () => [props.notesList, props.lineationList],
      (newValue, oldValue) => {
        if (newValue[0].length > oldValue[0].length) {
          highlightedNotes(newValue[0][newValue[0].length - 1]);
        }
        if (newValue[1].length > oldValue[1].length) {
          highlightedUnderline(newValue[1][newValue[1].length - 1]);
        }
      }
    );

    // 电子书的解析和渲染
    function showEpub() {
      data.book = new (Epub as any)(props.bookPath);
      const Book = toRaw(data.book as any);
      data.rendition = Book.renderTo("epubReadBox", {
        width: props.innerWidth,
        height: props.innerHeight,
        // method: "default",
        // manager: "continuous",
        flow: data.flow,
      });
      // 通过Rendtion.display渲染电子书

      if (props.location) {
        // 从上次阅读位置开始阅读
        toRaw(data.rendition as any).display(props.location);
      } else {
        toRaw(data.rendition as any).display();
      }

      // Book对象的钩子函数ready
      Book.ready
        .then(() => {
          data.navigation = toRaw(Book.navigation.toc);
          setTimeout(() => {
            data.loadingShow = false;
          }, 4000);
          // 生成Locations对象
          return Book.locations.generate();
        })
        .then(() => {
          // 保存locations对象
          data.locations = Book.locations;
          // 渲染笔记

          // 标记电子书为解析完毕状态
          setTimeout(() => {
            const currentLocation = toRaw(
              data.rendition as any
            ).currentLocation();
            getReadingPosition(currentLocation.start);
            // jumpDirectory(toRaw(data.navigation as any)[1]);
            renderingNotes();
          }, 2000);
          toRaw(data.book as any).rendition.flow(data.flow);
        });
      // 设置字体大小
      setFontSize(data.defaultFontSize);
      // 设置主题
      registerTheme();
      // 监听阅读进度
      toRaw(data.rendition as any).on("relocated", (location: any) => {
        getReadingPosition(location.start);
      });

      // 绑定触摸开始事件
      toRaw(data.rendition as any).on("touchstart", (e: any) => {
        // e.preventDefault(); //阻止浏览器长按显示菜单
        touchstart(e);
      });

      // 绑定触摸中事件
      toRaw(data.rendition as any).on("touchmove", (e: any) => {
        touchmove(e);
      });

      // 绑定触摸结束事件
      toRaw(data.rendition as any).on("touchend", (e: any) => {
        touchend(e);
      });

      toRaw(data.rendition as any).on(
        "selected",
        (cfiRange: any, contents: any) => {
          data.cfiRange = cfiRange; //笔记选取

          data.noteState = "add";
          if (props.isNote == "yes" && data.cfiRange != "") {
            data.showToolbar = true;
          }
        }
      );
      // 绑定选择文本事件
      toRaw(data.rendition as any).hooks.content.register(
        (
          contents: {
            document: {
              addEventListener: (
                arg0: string,
                arg1: (e: { preventDefault: () => void }) => boolean
              ) => void;
            };
          },
          view: any
        ) => {
          contents.document.addEventListener("contextmenu", (e: any) => {
            e.preventDefault();
            if (props.isNote == "yes" && data.cfiRange != "") {
              data.showToolbar = true;
            }

            data.toolbarStyle = {
              top: e.offsetY + 320 + "px",
              left: e.offsetX + "px",
            };

            return false;
          });
        }
      );
    }

    // 获取当前阅读位置
    function getReadingPosition(start: any) {
      data.currentCfi = start;
      data.nowtitle = start.href;
      if (toRaw(data.locations as any)._locations) {
        const progress =
          Math.floor(
            toRaw(data.locations as any)
              .percentageFromCfi(start.cfi)
              .toFixed(5) * 10000
          ) / 100;
        data.readProgress = progress;
      }

      if (props.fileId) {
        localStorage.setItem("location_" + props.fileId, start.cfi);
      }
    }

    // 切换章节
    function switchChapter(type: any) {
      for (let i = 0; i < data.navigation.length; i++) {
        if (
          JSON.stringify(toRaw(data.navigation as any)[i]).indexOf(
            toRaw(data.nowtitle as any)
          ) !== -1
        ) {
          if (type === "nextChapter" && i < data.navigation.length) {
            if (props.limitSize > data.readProgress) {
              //下一章
              jumpDirectory(toRaw(data.navigation as any)[i + 1]);
            } else if (props.limitSize < 100) {
              promptAppRead();
            }
          } else if (type === "prevChapter" && i > 0) {
            //上一章
            jumpDirectory(toRaw(data.navigation as any)[i - 1]);
          }
        }
      }
    }

    // 跳转到当前目录位置
    function jumpDirectory(item: any) {
      const currentLocation = toRaw(data.rendition as any).currentLocation();
      toRaw(data.rendition as any).display(item.href);
      setTimeout(() => {
        const Location = toRaw(data.rendition as any).currentLocation();
        const progress =
          Math.floor(
            toRaw(data.locations as any)
              .percentageFromCfi(Location.start.cfi)
              .toFixed(5) * 10000
          ) / 100;

        if (progress > props.limitSize) {
          designatedSpot(currentLocation.start.cfi);
          promptAppRead();
        } else {
          toRaw(data.menuBarBox as any).popupShow = false;
          hideMenubar();
        }
      }, 500);
    }

    // 拖动阅读进度条 跳转页面
    function readChange(e: number) {
      const percentage = e / 100;
      const location =
        percentage > 0
          ? toRaw(data.locations as any).cfiFromPercentage(percentage)
          : 0;

      if (props.limitSize > percentage * 100) {
        toRaw(data.rendition as any).display(location);
      } else {
        // 阅读进度超过限制提示请到app阅读
        promptAppRead();
      }
    }

    // 提示到app阅读
    function promptAppRead() {
      data.readProgress = props.limitSize;
      Toast("仅可试读前10%");
      // myUni.postMessage({
      //   data: {
      //     name: "goApp",
      //   },
      // });
    }

    // 跳到指定位置
    function designatedSpot(location: any) {
      toRaw(data.rendition as any).display(location);
    }

    // 修改阅读器亮度
    function brightnessChange(e: number) {
      myUni.postMessage({
        data: {
          name: "brightness",
          value: e,
        },
      });
    }

    // 上一页
    function prevPage() {
      if (data.rendition && data.flow == "paginated") {
        toRaw(data.rendition as any).prev();
      }
    }

    // 下一页
    function nextPage() {
      if (props.limitSize > data.readProgress) {
        if (data.rendition && data.flow == "paginated") {
          toRaw(data.rendition as any).next();
        }
      } else if (props.limitSize < 100) {
        promptAppRead();
      }
    }

    // 设置主题
    function setTheme(index: number) {
      data.defaultTheme = index;
      registerTheme();
      saveTheme();
    }

    // 切换阅读模式
    function readPattern() {
      if (props.systemType != "ios") {
        hideMenubar();
        data.flow = data.flow === "scrolled" ? "paginated" : "scrolled";
        toRaw(data.book as any).rendition.flow(data.flow);
      }
    }

    // 修改字号大小
    function FontSizeBtn(size: number) {
      if (
        data.defaultFontSize + size >= 12 &&
        data.defaultFontSize + size <= 40
      ) {
        data.defaultFontSize += size;
        setFontSize(data.defaultFontSize);
      }
    }

    // 设置字号大小
    function setFontSize(fontSize: number) {
      data.defaultFontSize = fontSize;
      toRaw(data.rendition as any).themes.fontSize(fontSize + "px");
      saveTheme();
    }

    // 设置字体
    function fontFamily(item: { famiy: any; index: number }) {
      toRaw(data.rendition as any).themes.font(item.famiy);
      data.defaultFontFamily = item.index;
      saveTheme();
    }

    // 保存主题到缓存中
    function saveTheme() {
      let theme = {
        defaultTheme: data.defaultTheme,
        defaultFontFamily: data.defaultFontFamily,
        defaultFontSize: data.defaultFontSize,
        flow: data.flow,
      };
      localStorage.setItem("xml-mobile-epub-theme", JSON.stringify(theme));
    }

    function menuClick(item: any) {
      if (item.name == "进度") {
        data.showProgress = true;
        data.setUpShow = false;
      } else if (item.name == "亮度") {
        data.setUpShow = false;
        data.showBrightness = true;
      } else if (item.name == "设置") {
        data.setUpShow = true;
        data.showProgress = false;
        data.showBrightness = false;
      }
    }

    // 处理触摸开始事件
    function touchstart(e: any) {
      data.touchStartY = e.changedTouches[0].clientY;
      data.touchStartX = e.changedTouches[0].clientX;
      data.touchStartTime = e.timeStamp;
      const Path = e.path || (e.composedPath && e.composedPath()) || [];
      if (e.changedTouches[0].clientY < window.innerHeight) {
        if (Path[Path.length - 3].clientHeight <= window.innerHeight) {
          data.isPageTop = true;
          data.isPageBottom = true;
        } else {
          data.isPageBottom = false;
          data.isPageTop = true;
        }
      } else if (
        parseInt(Path[2].clientHeight) - parseInt(e.changedTouches[0].clientY) <
        window.innerHeight
      ) {
        data.isPageTop = false;
        data.isPageBottom = true;
      } else {
        data.isPageTop = false;
        data.isPageBottom = false;
      }
    }

    // 处理触摸中事件
    function touchmove(e: any) {
      updateDragDistance(e.touches[0].clientY - data.touchStartY); //更新拖拽加载动画数据
    }

    // 处理触摸结束事件
    function touchend(e: any) {
      const offsetX = e.changedTouches[0].clientX - data.touchStartX;
      const time = e.timeStamp - data.touchStartTime;

      if (time < 300 && offsetX > 20) {
        prevPage();
      } else if (time < 300 && offsetX < -20) {
        nextPage();
      } else if (offsetX == 0) {
        console.log(e);

        if (e.srcElement.tagName === "IMG") {
          //如果点击的是图片，查看大图
          (ImagePreview as any)([e.srcElement.src]);
        } else if (e.target.className.indexOf("show-add-notes") !== -1) {
          data.showAddNotes = true;
        } else if (e.target.className.indexOf("add-underline") !== -1) {
          addUnderline();
        } else if (e.srcElement.tagName !== "A") {
          // 点击的不是a标签且不是选中文字状态，显示菜单
          data.titleBarShow = !data.titleBarShow;
          data.menuBarShow = !data.menuBarShow;
          data.setUpShow = false;
          data.showProgress = false;
          data.showBrightness = false;
          data.showToolbar = false;
        }
      }
      clearDragDistance(); //清除拖拽加载动画数据
    }

    // 更新拖拽加载动画数据
    function updateDragDistance(topY: any) {
      if (
        topY > 0 &&
        topY < 180 &&
        data.isPageTop &&
        data.flow === "scrolled"
      ) {
        data.dragDistance = topY > data.dragDistance ? topY : data.dragDistance;
        data.dragLocation = "prevChapter";
      } else if (
        topY < 0 &&
        topY > -180 &&
        data.isPageBottom &&
        data.flow === "scrolled"
      ) {
        data.dragDistance = topY < data.dragDistance ? topY : data.dragDistance;
        data.dragLocation = "nextChapter";
      }
    }

    // 清除拖拽加载动画数据
    function clearDragDistance() {
      if (data.dragDistance != 0) {
        const countDown =
          data.dragDistance > 140 ? 1000 : data.dragDistance < -140 ? 1000 : 0;
        setTimeout(() => {
          let time = setInterval(() => {
            if (data.dragDistance > 0) {
              data.dragDistance -= 10;
            } else if (data.dragDistance < 0) {
              data.dragDistance += 10;
            }
            if (
              (data.dragDistance > 0 && data.dragDistance <= 10) ||
              (data.dragDistance < 0 && data.dragDistance >= -10)
            ) {
              data.dragDistance = 0;
              clearInterval(time);
            }
          }, 16);
          if (data.dragDistance > 140 || data.dragDistance < -140) {
            switchChapter(data.dragLocation);
          }
        }, countDown);
      }
    }

    // 注册主题
    function registerTheme() {
      toRaw(data.rendition as any).themes.default({
        "::selection": {
          background: "rgba(255,255,0, 0.3)",
        },
        ".epubjs-hl": {
          fill: "yellow",
          "fill-opacity": "0.3",
          "mix-blend-mode": "multiply",
        },
        body: {
          background: "rgba(255,255,255, 0)",
          "max-width": "100%vw important",
          "background-image": "none",
          "-webkit-touch-callout": "none",
          color: themeList[data.defaultTheme].style.body.color,
        },
        ".tp-c": {
          "text-indent": "0",
        },
        "h1,h2,h3,h4,h5,h6": {
          "font-size": "inherit!important",
        },
        p: {
          "text-indent": "0",
          "line-height": "2em",
          "word-break": "break-word",
          "font-size": "inherit!important",
          color: themeList[data.defaultTheme].style.body.color,
        },
        img: {
          "max-width": "100%!important",
        },
        video: {
          "max-width": "100%!important",
        },
        pre: {
          "white-space": "pre-wrap",
          "word-wrap": "break-word",
        },
        "audio::-webkit-media-controls": {
          overflow: "hidden !important",
        },
        "audio::-webkit-media-controls-enclosure": {
          width: "calc(100% + 32px)",
          " margin-left": "auto",
        },
      });
    }

    // 隐藏菜单栏
    function hideMenubar() {
      data.titleBarShow = false;
      data.menuBarShow = false;
      data.setUpShow = false;
      data.showProgress = false;
      data.showBrightness = false;
      data.showToolbar = false;
    }

    // 渲染笔记
    function renderingNotes() {
      props.notesList.map((item: any) => {
        highlightedNotes(item);
      });
      props.lineationList.map((item: any) => {
        highlightedUnderline(item);
      });
    }
    // 高亮笔记
    function highlightedNotes(item: any) {
      // console.log("高亮笔记", item);
      // notesList.map((item: any) => {
      if (item.textRange) {
        toRaw(data.rendition as any).annotations.remove(
          item.textRange,
          "highlight"
        );
        toRaw(data.rendition as any).annotations.highlight(
          item.textRange,
          {
            id: item.id,
            noteContent: item.noteContent,
            type: item.type,
          },
          (e: any) => {
            setTimeout(() => {
              if (props.isNote == "yes") {
                data.modifyNotes = true;
                data.modifyNotesType = item.type;
              }
              data.notelsid = item.id;
              data.textRange = item.textRange;
            }, 500);
          }
        );
      }
      // });
    }

    // 删除高亮笔记
    // function removeNotes(notesList: any[]) {
    //   // console.log("删除高亮笔记");

    //   notesList.map((item: any) => {
    //     if (item.textRange) {
    //       toRaw(data.rendition as any).annotations.remove(
    //         item.textRange,
    //         "highlight"
    //       );
    //     }
    //   });
    // }

    function addUnderline() {
      let note = {
        originalText: "",
        textRange: data.cfiRange,
        href: "",
        noteContent: "",
      };
      data.showToolbar = false;
      context.emit("addUnderline", note);
      // toRaw(data.rendition as any).annotations.add("underline", data.cfiRange);
    }

    // 高亮划线
    function highlightedUnderline(item: any) {
      // console.log("高亮划线", item);
      // lineationList.map((item: any) => {
      if (item.textRange) {
        toRaw(data.rendition as any).annotations.remove(
          item.textRange,
          "highlight"
        );
        toRaw(data.rendition as any).annotations.highlight(
          item.textRange,
          {
            id: item.id,
            type: item.type,
          },
          (e: any) => {
            setTimeout(() => {
              if (props.isNote == "yes") {
                data.modifyNotes = true;
                data.modifyNotesType = item.type;
              }
              data.notelsid = item.id;
              data.textRange = item.textRange;
            }, 500);
          },
          "",
          {
            stroke: "red",
            "fill-opacity": 0,
          }
        );
      }
      // });
    }

    // 删除高亮笔记
    function removeNotes(textRange: string) {
      // console.log("删除高亮笔记");

      // lineationList.map((item: any) => {
      toRaw(data.rendition as any).annotations.remove(textRange, "highlight");
      // });
    }
    // 删除高亮划线
    function removeUnderline(textRange: string) {
      // console.log("删除高亮笔记");
      // lineationList.map((item: any) => {
      toRaw(data.rendition as any).annotations.remove(textRange, "highlight");
      // });
    }

    // 取消添加笔记
    function cancelAddNotes() {
      data.showToolbar = false;
      data.showAddNotes = false;
    }

    // 确认添加笔记
    function confirmAddNotes(e: any) {
      if (data.noteState == "add") {
        //添加笔记
        toRaw(data.book as any)
          .getRange(data.cfiRange)
          .then((range: any) => {
            if (range) {
              const note = {
                originalText: range.toString(),
                textRange: data.cfiRange,
                href: "#" + data.cfiRange,
                noteContent: e.trim(),
              };
              context.emit("confirmAddNotes", note);
            }
          });
      } else {
        data.bookNote.noteContent = e.trim();
        context.emit("confirmAmendNotes", data.bookNote);
      }
      data.showToolbar = false;
      data.showAddNotes = false;
    }

    // 修改笔记
    function updateNotes() {
      props.notesList.map((item: any) => {
        if (item.id === data.notelsid) {
          data.bookNote = item;
          data.notesContent = item.noteContent;
          data.noteState = "amend";
          data.modifyNotes = false;
          data.showAddNotes = true;
        }
      });
    }

    // 删除笔记
    function deleteNotes() {
      Dialog({
        title: "提示",
        message: "确认删除吗?",
        showCancelButton: true,
      })
        .then(() => {
          data.modifyNotes = false;
          let bookData = {
            noteId: data.notelsid,
          };
          context.emit("confirmDeleteNotes", bookData, "note");
          removeNotes(data.textRange);
        })
        .catch(() => {
          // on cancel
        });
    }

    // 删除划线
    function deleteLineation() {
      data.modifyNotes = false;
      let bookData = {
        noteId: data.notelsid,
      };
      removeUnderline(data.textRange);
      context.emit("confirmDeleteNotes", bookData, "lineation");
    }

    // 全局搜索
    function xdoSearch(value: string) {
      return Promise.all(
        toRaw(data.book as any).spine.spineItems.map((item: any) =>
          item
            .load(toRaw(data.book as any).load.bind(toRaw(data.book as any)))
            .then(item.find.bind(item, value))
            .finally(item.unload.bind(item))
        )
      ).then((results) => Promise.resolve([].concat.apply([], results)));
    }

    // 格式化搜索内容
    function formatSearch() {
      xdoSearch(data.searchValue).then((result) => {
        let searchList = result.map((item: any) => {
          // item.excerpt = item.excerpt.replace(
          //   q,
          //   `<span class="content-search-text" style="color: #2e9adb;
          //    font-size: 13px;
          //    font-family: PingFang SC;
          //    font-weight: 400;">${q}</span>`
          // );
          let replaceReg = new RegExp(data.searchValue, "ig");
          let replaceString = `<span class="content-search-text" style="color: #2e9adb">${data.searchValue}</span>`;
          item.excerpt = item.excerpt.replace(replaceReg, replaceString);

          return item;
        });
        data.searchList = searchList || [];
      });
    }

    // 跳转到搜索位置
    function searchcfi(item: any) {
      data.searchModule = false;
      toRaw(data.rendition as any).display(item.cfi);
      data.searchValue = "";
      data.searchList = [];
    }

    // 添加书签
    function addBookmark() {
      const currentLocation = toRaw(data.rendition as any).currentLocation();
      const cfibase = currentLocation.start.cfi.replace(/!.*/, "");
      const cfistart = currentLocation.start.cfi
        .replace(/.*!/, "")
        .replace(/\)$/, "");
      const cfiend = currentLocation.end.cfi.replace(/.*!/, "");
      const cfirange = `${cfibase}!,${cfistart},${cfiend}`;

      toRaw(data.book as any)
        .getRange(cfirange)
        .then((range: any) => {
          const note = {
            originalText: "",
            textRange: toRaw(data.currentCfi as any).cfi,
            href: "#" + toRaw(data.currentCfi as any).href,
            noteContent: range.toString(),
          };
          context.emit("addBookmark", note);
        });
    }
    // 删除笔记
    function deleteNote(note: any) {
      data.notelsid = note.id;
      deleteNotes();
    }
    // 删除书签
    function deleteBookmark(note: any) {
      console.log("删除书签");

      context.emit("deleteBookmark", { noteId: note.id, ...note });
    }

    onMounted(() => {
      showEpub();
      // data.menuBarBox=getCurrentInstance()
      // setInterval(()=>{
      //   Toast(data.cfiRange)
      // },8000)
    });

    return {
      data,
      ...toRefs(data),
      themeList,
      touchstart,
      touchmove,
      touchend,
      readPattern,
      jumpDirectory,
      setTheme,
      FontSizeBtn,
      fontFamily,
      readChange,
      brightnessChange,
      hideMenubar,
      menuClick,
      cancelAddNotes,
      confirmAddNotes,
      designatedSpot,
      updateNotes,
      deleteNotes,
      xdoSearch,
      formatSearch,
      searchcfi,
      addUnderline,
      addBookmark,
      deleteNote,
      deleteBookmark,
      deleteLineation,
      removeNotes,
    };
  },
});
