
import { defineComponent, getCurrentInstance } from "vue";
import { themeList } from "../state/ts/config";

export default defineComponent({
  name: "titleBar",
  props: {
    defaultTheme: {
      type: Number,
      default: 0,
    },
    titleBarShow: {
      type: Boolean,
      default: false,
    },
    integralBox: {
      type: Boolean,
      default: false,
    },
    integral: {
      type: Number,
      default: 15,
    },
    width: {
      type: Number,
      default: 22, //max:80
    },
    sourceNumber: {
      type: Number,
      default: 0,
    },
    bookmarkList: {
      type: Array,
      default: () => [],
    },
    currentCfi: {
      type: Object,
      default: () => {},
    },
  },
  emits: ["switchover", "search", "addBookmark", "deleteBookmark"],
  setup(props, context) {
    // const myUni =
    //   getCurrentInstance()?.appContext.config.globalProperties.$myUni;

    // 切换文件
    function switchFiles() {
      context.emit("switchover");
      // myUni.postMessage({
      //   data: {
      //     name: "changewenjian",
      //   },
      // });
    }
    // 全文搜索
    function search() {
      context.emit("search");
    }
    // 添加书签
    function addBookmark() {
      context.emit("addBookmark");
    }
    // 删除书签
    function deleteBookmark() {
      props.bookmarkList.map((item: any) => {
        console.log(item, props.currentCfi.cfi);

        if (item.textRange == props.currentCfi.cfi) {
          context.emit("deleteBookmark", item);
        }
      });
    }

    return {
      themeList,
      switchFiles,
      search,
      addBookmark,
      deleteBookmark,
    };
  },
});
