import { createApp } from "vue";
import App from "./App.vue";
import router from "./router";
// 引入自定义组件
import xmlComponents from "./index";
import "./xmlComponents/xmlEpubRead/state/icon/iconfont.css";

import * as uni from "./xmlComponents/xmlEpubRead/state/js/uniwebview.js";
document.addEventListener("UniAppJSBridgeReady", function () {});

// 引入vant
import { Slider, Icon, Stepper, Popup, Toast, Empty } from "vant";
import "vant/lib/index.css";
const app = createApp(App);
app.config.globalProperties.$myUni = uni;
app
  .use(router)
  .use(xmlComponents)
  .use(Slider)
  .use(Icon)
  .use(Stepper)
  .use(Popup)
  .use(Toast)
  .use(Empty)
  .mount("#app");
