
import { defineComponent, watch, ref } from "vue";
import { Toast } from "vant";
import { themeList } from "../state/ts/config";
export default defineComponent({
  name: "inputField",
  props: {
    notesContent: {
      type: String,
      default: "",
    },
    noteState: {
      type: String,
      default: "add", //add:添加，amend：修改
    },
    defaultTheme:{
      type:Number,
      default:0,
    }
  },
  emits: ["cancel", "confirm"],
  setup(props, context) {
    watch(
      () => props.notesContent,
      (newValue) => {
        content.value = newValue;
      }
    );

    let content = ref("");
    // 关闭目录
    function cancel() {
      context.emit("cancel");
    }

    // 确认添加笔记
    function confirm() {
      if (content.value.replace(/\s+/g, "") === "") {
        Toast("请输入笔记内容");
      } else if (content.value.length > 200) {
        Toast("最多输入200个字符");
      } else {
        context.emit("confirm", content.value);
      }
      content.value = "";
    }

    return {
      content,
      themeList,
      cancel,
      confirm,
    };
  },
});
