
import { defineComponent, ref, toRaw } from "vue";

export default defineComponent({
  name: "settingUp",
  props: [
    "themeList",
    "themeShow",
    "fontSize",
    "themeIndex",
    "defaultFontFamily",
  ],
  emits: ["close", "setTheme", "change", "fontFamily"],
  setup(props, context) {
    let value = toRaw(props).fontSize;
    let fontFamilyList = [
      {
        value:
          "'Microsoft YaHei',PingFangSC-Regular,HelveticaNeue-Light,'Helvetica Neue Light',sans-serif",
        label: "雅黑",
        index: 0,
      },
      {
        value: "STKaiti,Kaiti",
        label: "楷书",
        index: 1,
      },
    ]; //字体列表
    //   let defaultFontFamily=ref(0)

    // 关闭目录
    function close() {
      context.emit("close");
    }

    // 跳转到当前目录位置
    function setTheme(index: number) {
      context.emit("setTheme", index);
    }

    // 修改阅读字体大小
    function change(e: any) {
      context.emit("change", e);
    }

    // 修改阅读字体
    function fontFamily(value: string, index: number) {
      context.emit("fontFamily", { value: value, index: index });
    }
    return {
      value,
      fontFamilyList,
      close,
      setTheme,
      change,
      fontFamily,
      // defaultFontFamily
    };
  },
});
