
import { defineComponent, reactive, toRefs, onMounted } from "vue";
import { fontFamilyList, themeList, menuList } from "../state/ts/config";
import { Dialog } from "vant";
import "../state/icon/iconfont";
export default defineComponent({
  name: "catalogue",
  props: {
    menuBarShow: {
      type: Boolean,
      default: false,
    },
    setUpShow: {
      type: Boolean,
      default: false,
    },
    showProgress: {
      type: Boolean,
      default: false,
    },
    showBrightness: {
      type: Boolean,
      default: false,
    },
    nowtitle: {
      type: String,
      default: "",
    },
    navigation: {
      type: Array,
      default: [
        {
          href: "",
          label: "",
          subitems: [
            {
              href: "",
              label: "",
              subitems: [],
            },
          ],
        },
      ],
    },
    notesList: {
      type: Array,
      default: [],
    },
    bookmarkList: {
      type: Array,
      default: [],
    },
    defaultTheme: {
      type: Number,
      default: 0,
    },
    defaultFontSize: {
      type: Number,
      default: 0,
    },
    defaultFontFamily: {
      type: Number,
      default: 0,
    },
    readProgress: {
      type: Number,
      default: 0,
    },
    brightness: {
      type: Number,
      default: 0,
    },
    luminance: {
      //菜单栏是否显示亮度
      type: String,
      default: "luminance",
    },
    systemType: {
      type: String,
      default: "",
    },
  },
  emits: [
    "close",
    "jumpDirectory",
    "setTheme",
    "progress",
    "readPattern",
    "FontSize",
    "fontFamily",
    "readChange",
    "brightnessChange",
    "menuClick",
    "seeNotes",
    "deleteBookmark",
    "deleteNote",
  ],
  setup(props, context) {
    let data = reactive({
      popupShow: false,
      readProgress: props.readProgress,
      brightness: props.brightness,
      current: 0,
      directoryHref: [] as any[],
      directoryLabel: [] as any[],
    });
    const menus = reactive([] as any[]);

    if (props.luminance == "progress") {
      //如果不显示亮度，则显示阅读进度
      menuList[1].name = "进度";
    }

    function menuClick(item: any) {
      if (item.name == "滑动" && props.systemType != "ios") {
        menuList[2].name = "翻页";
        context.emit("readPattern", item);
      } else if (item.name == "翻页" && props.systemType != "ios") {
        menuList[2].name = "滑动";
        context.emit("readPattern", item);
      } else if (item.name == "目录") {
        data.popupShow = true;
      } else {
        context.emit("menuClick", item);
      }
    }
    // 关闭目录
    function close() {
      context.emit("close");
    }

    // 跳转到当前目录位置
    function jumpDirectory(item: any) {
      // setTimeout(()=>{
      //   data.popupShow = false;
      // },600)
      context.emit("jumpDirectory", item);
    }

    // 更换主题
    function setTheme(index: any) {
      context.emit("setTheme", index);
    }

    // 修改字号大小
    function FontSize(number: Number) {
      context.emit("FontSize", number);
    }

    // 切换阅读字体
    function fontFamily(item: any, index: any) {
      context.emit("fontFamily", { famiy: item, index: index });
    }

    // 切换阅读模式
    function readChange(e: any) {
      context.emit("readChange", e);
    }

    // 切换阅读器亮度
    function brightnessChange(e: any) {
      context.emit("brightnessChange", e);
    }

    // 切换目录和笔记
    function changecurrent(e: any) {
      data.current = e;
    }

    // 查看笔记
    function seeNotes(item: any) {
      data.popupShow = false;
      context.emit("seeNotes", item.textRange);
    }

    function deleteBookmark(item: any) {
      Dialog({
        title: "提示",
        message: "确认删除吗?",
        showCancelButton: true,
      })
        .then(() => {
          context.emit("deleteBookmark", item);
        })
        .catch(() => {
          // on cancel
        });
    }
    function deleteNote(item: any) {
      context.emit("deleteNote", item);
    }

    return {
      ...toRefs(data),
      menuList,
      fontFamilyList,
      themeList,
      menuClick,
      close,
      jumpDirectory,
      setTheme,
      FontSize,
      fontFamily,
      readChange,
      brightnessChange,
      changecurrent,
      seeNotes,
      deleteBookmark,
      deleteNote,
    };
    context.expose({
      data,
    });
  },
});
