
import { defineComponent } from "vue";

export default defineComponent({
  name: "catalogue",
  props: ["navigation", "navShow", "nowtitle", "defaultTheme"],
  emits: ["close", "jumpDirectory"],
  setup(props, context) {
    // 关闭目录
    function close() {
      context.emit("close");
    }

    // 跳转到当前目录位置
    function jumpDirectory(item: any) {
      context.emit("jumpDirectory", item);
    }

    return {
      close,
      jumpDirectory,
    };
  },
});
